html { background: #fff; max-width: 600px; padding: 1em; box-sizing: border-box;}

.tags { list-style-type: none; display: flex; margin-left: 0; flex-wrap: wrap;}

.tag { color: blue; padding: 0.5em; text-decoration: none; margin-right: 1em; margin-left: 0; border: 1px solid blue; transition: box-shadow .2s; background: white; }

.tag:hover {
    box-shadow: 2px 2px 0px #094BF5;
}

.numberLabel { padding: 0.5em; background: #BED4FF; color: blue; margin-right: -0.5em; margin-left: 0.5em;}

.pattern-card { padding: 0.5em; background: #F5F5F5; margin: 1em 1em 1em 0em; transition: box-shadow .2s;}

.pattern-card:hover { box-shadow: 2px 2px 0px #094BF5; }

.post-list { list-style-type: none; }

.titleFlex {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1em;
}

.date  {
    margin: 0;
    color: #585858;
    text-decoration: none;
}

h2 { text-decoration: none; color: blue; margin: 0;}

ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
}

li {
    padding: 1em 0 1em 0;
}